<template>
    <div>
        <template v-if="canteenList.length!=0">
             <canteen-card :list='canteenList' @goTo='goInCanteen'></canteen-card>
        </template> 
    </div>
</template>
<script>
import {getUserCollet} from '@/api/api.js'
import {CanteenCard} from '_c/canteen/index.js'
export default {
    components:{CanteenCard},
    data(){
        return{
            user:'',
            canteenList:[]
        }
    },
    mounted(){
        this.user=JSON.parse(localStorage.getItem('user'))
        let userId=this.user.userId
        let data={userId:userId}
        getUserCollet(data).then(res=>{
           this.canteenList=res.data.data
        },err=>{
            this.$Message.error('网络错误')
        })
    },
    methods:{
          goInCanteen(canteenId){
            this.$router.push({name:'clientFoodList',query:{canteenId:canteenId}})
        },
    }
}
</script>
<style lang="less">

</style>


